.App {
  /* border: 5px solid rgb(65, 255, 17); */

  /* text-align: center; */
  background-color: #222738;
  z-index: -1;
  color: white;
  margin: 0;
}



.App-header {
  width: 100%;
  background-color: #2c3242;
  color: white;
  /* border:   3px dashed #ff3849; */
  /* font-size: calc(5px + 2vmin); */
  display:flex;
}
