  position: relative;
  bottom:10px;
  /* border: 2px dashed #387bff; */
}

.navbar {
  /* border: 3px dashed rgb(255, 112, 17); */
  font-size: 20px;
  font-weight: 600;
  max-width: 500px;
  float: right;
}
.Header_links {
  /* border: 3px dashed rgb(65, 255, 17); */
  cursor: pointer;
  margin: 25px;
}
.Header_link {
  /* border: 3px dashed rgb(65, 255, 17); */
  /* margin: 120px; */
  /* padding-right: 20px; */
  border-bottom: 3px solid #222738;
  margin: 10px;
  display: inline-table;

}
.Header_link:hover {
  border-bottom: 3px solid #adbffc;
	transition: all 0.5s ease;
  /* font-size: 19px; */

  /* background-color: #15171d; */
  color: #d6d8db;

}

.about_hide {
  display: none;
	transition: all 0.5s ease;
}

.about_show {
  display:block;
  margin: 10px;
	transition: all 0.5s ease;

}
.animals_list{
  display: block;
}
